@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:'Roboto', sans-serif}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.btn{padding:14px;font-size:1rem;color:#fff;border:none;border-radius:5px;cursor:pointer;white-space:nowrap}.show{transition-delay:all 0.3s ease-in;display:-webkit-flex;display:flex}.hide{transition-delay:all 0.3s ease-in;display:none}@media only screen and (max-width: 767px){.noneMobile{display:none !important}body{display:-webkit-flex;display:flex}}::-webkit-scrollbar{background-color:#fff;width:16px}::-webkit-scrollbar-track{background-color:#fff}::-webkit-scrollbar-thumb{background-color:#babac0;border-radius:16px;border:4px solid #fff}::-webkit-scrollbar-button{display:none}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.header{width:100%;height:100px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;box-sizing:border-box;padding:20px}.header .logo{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.header .logo .logoLink{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;text-decoration:none}.header .logo .logoLink img{width:124px}.header .logo .logoLink .help-text{font-size:22px;padding-left:4px;color:#5f6368;margin-top:-4px}.header .action-btn{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.header .action-btn .icons-block{padding:0px 30px;display:-webkit-flex;display:flex}@media (max-width: 767px){.header .action-btn .icons-block{padding:0px 5px}}.header .action-btn .icon{padding:10px 10px;font-size:20px;color:#5f6368}.header .action-btn .icon:hover{cursor:pointer;border-radius:50%;background-color:#e3e3e3}.header .action-btn .profile{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;border-radius:50%}.header .action-btn .profile .icon-app{padding:10px 12px;margin-right:20px;font-size:20px}.header .action-btn .profile .icon-app:hover{cursor:pointer;border-radius:50%;background-color:#e3e3e3}@media (max-width: 767px){.header .action-btn .profile .icon-app{margin-right:5px}}.header .action-btn .profile .profileImg{display:-webkit-flex;display:flex;border-radius:50%;transition:all 0.2s ease-in}.header .action-btn .profile .profileImg:hover{-webkit-transform:scale(1.1);transform:scale(1.1);transition:all 0.2s ease-out}.header .action-btn .profile .profileImg img{width:50px;height:50px;border-radius:50%}@media (max-width: 425px){.header .action-btn .date-block{display:none}}

.nomatch-container{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column-reverse;flex-direction:column-reverse;color:#fff;height:calc(100vh - 250px);background:#020024;background:linear-gradient(90deg, #020024 0%, #090979 0%, #00d4ff 100%)}.nomatch-container .h1{font-size:100px;font-weight:300;padding:0;margin:0}.nomatch-container .h1:hover{text-shadow:2px 3px 8px rgba(114,0,0,0.8)}.nomatch-container .h3{padding:20px;font-size:30px;font-weight:300}.nomatch-container .btn-primary{color:#1a73e8;background:#fff;text-decoration:none}

